import React, { useEffect, useState } from "react"
import axios from "axios"
import SearchPlayer from "../components/Search"
import PlayerInfo from '../components/PlayerInfo'
import SojuSquad from "../components/SojuSquad"
import SelectedPlayers from "../components/SelectedPlayers"
import Loader from "../components/Loader"
import Message from "../components/Message"
import GameModal from "../components/GameModal"
import classes from "./HomeScreen.module.css"
import sojuSquad from "../assets/data"

const HomeScreen = () => {
  const [player, setPlayer] = useState('')
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('')
  const [selectedPlayers, setSelectedPlayers] = useState([])

  useEffect(() => {

    setData(null)
    if (player !== "") {
      setLoading(true)
      const apiUrl = `/api/players/${player}`;
      axios.get(apiUrl)
        .then(response => {
          setData(response.data);
          setLoading(false);
          setError('')
        })
        .catch(error => {
          if (error.response && error.response.status === 404) {
            setError('Player not found.');
          } else {
            setError('An error occurred while fetching data.');
          }
          setLoading(false);
        });
    }
  }, [player]);

  useEffect(() => {
    if (selectedPlayers.length === 5) {
      setData(null);
    }
  }, [selectedPlayers]);

  const handleSearch = (enteredPlayer) => {
    console.log('Searching for:', enteredPlayer);
    setPlayer(enteredPlayer)
  };

  const handleAddPlayer = (player) => {
    if (selectedPlayers.length < 5) {
      if (!selectedPlayers.some((p) => p.id === player.id)) {
        setSelectedPlayers((prevPlayers) => [...prevPlayers, player]);
        console.log('Player added: ', player);
      }
    }
  };

  const handleRemovePlayer = (player) => {
    const updatedPlayers = selectedPlayers.filter((p) => p.id !== player.id);
    setSelectedPlayers(updatedPlayers);
    console.log('Player Removed: ', player);
  };
  
  const renderLoading = () => <Loader />;
  const renderPlayerInfo = () => data && <PlayerInfo playerData={data} onAddPlayer={handleAddPlayer} />;
  const renderError = () => error && <Message variant="error" children={error} />;

  return (
    <div>
      <div className={classes['animation-wrapper']}>
        <div className={classes.animation}>
          Soju Squad vs. NBA
        </div>
      </div>
      <SojuSquad sojuSquad={sojuSquad}/>
      {selectedPlayers && <SelectedPlayers selectedPlayers={selectedPlayers} onRemovePlayer={handleRemovePlayer}/>}
      {selectedPlayers.length === 5 && <GameModal selectedPlayers={selectedPlayers} sojuSquad={sojuSquad}/>}
      {selectedPlayers.length < 5 && <SearchPlayer onSearch={handleSearch} loading={loading}/>}
      
      {loading ? renderLoading() : (
        <div>
          {renderPlayerInfo()}
          {renderError()}
        </div>
      )}
    </div>
  );
}

export default HomeScreen