import React, { useState, useEffect, useCallback } from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
};

const GameModal = ({ selectedPlayers, sojuSquad }) => {
    const [open, setOpen] = useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    const [shuffledPlayers, setShuffledPlayers] = useState([])
    const [playByPlayArray, setPlayByPlayArray] = useState([])
    const [playByPlayLines, setPlayByPlayLines] = useState([]);
    const [currentLineIndex, setCurrentLineIndex] = useState(0);
    const [winningTeam, setWinningTeam] = useState("")

    const shuffleArray = (selectedPlayers) => {
        const newArray = [...selectedPlayers]
        for (let i = newArray.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [newArray[i], newArray[j]] = [newArray[j], newArray[i]];
        }
        return newArray;
    }

    const updatePlayByPlayLines = useCallback(() => {
        setPlayByPlayLines((prevLines) => {
            const updatedLines = [...prevLines, playByPlayArray[currentLineIndex]];
            return updatedLines;
        });
    }, [playByPlayArray, currentLineIndex]);
    

    useEffect(() => {
        console.log("USE EFFECT 1")
        if (selectedPlayers.length === 5) {
            const shuffled = shuffleArray(selectedPlayers);
            setShuffledPlayers(shuffled);
        }
    }, [selectedPlayers]);

    useEffect(() => {
        console.log("USE EFFECT 2")
        if (shuffledPlayers.length === 5) {
            setPlayByPlayArray([
                `${shuffledPlayers[0].full_name} wins the tip, gathered by ${shuffledPlayers[1].full_name}<br />`,
                `${shuffledPlayers[1].full_name} dishes a beautiful no-look pass to ${shuffledPlayers[2].full_name}<br />`,
                `${shuffledPlayers[2].full_name} goes for the lay-up but is blocked by Michael<br />`,
                `Jordan cleans up the rebound and dribbles the ball up court<br />`,
                `Jordan finds Ash in the corner who hits a 3!<br />`,
                `[Challengers 0 - 3 Soju Squad]`,
                `${shuffledPlayers[3].full_name} inbounds the ball to ${shuffledPlayers[4].full_name} whos calling a play<br />`,
                `${shuffledPlayers[4].full_name} telling his team to clear out, he wants to iso Ash! <br />`,
                `Ash steals the ball and looks up to see Jordan who hasnt passed halfcourt, an open dunk! <br />`,
                `[Challengers 0 - 5 Soju Squad]`,
                `${shuffledPlayers[0].full_name} inbounds to ${shuffledPlayers[3].full_name} who pulls up for 3! <br />`,
                `Blocked by Victor! Gathered by Mike who sees the mismatch and throws the early pass to Jordan who finishes with a ferocious dunk! <br />`,
                `[Challengers 0 - 7 Soju Squad]`,
            ])
            console.log("Players have been shuffled and Array has been set")
        }
    }, [shuffledPlayers])

    useEffect(() => {
        console.log("USE EFFECT 3")
        if (open) {
            if (currentLineIndex < playByPlayArray.length) {
                const animationInterval = setInterval(() => {
                    setCurrentLineIndex((prevIndex) => {
                        const updatedIndex = prevIndex + 1;
                        console.log(updatedIndex);
    
                        if (updatedIndex === playByPlayArray.length) {
                            console.log("FINISHED");
                            clearInterval(animationInterval);
                        }
    
                        // Perform a task on the last iteration
                        if (updatedIndex === playByPlayArray.length) {
                            setWinningTeam("Soju Squad Wins!")
                        }
    
                        return updatedIndex;
                    });
                    updatePlayByPlayLines();
                }, 2000);
    
                return () => {
                    clearInterval(animationInterval);
                };
            }
        }
    }, [currentLineIndex, open, playByPlayArray, updatePlayByPlayLines]);
    

    return (
        <div>
            <h2 style={{ textAlign: "center", marginTop: "50px", fontFamily: "monospace" }}>Are you happy with your squad?</h2>
            <Button
                variant="contained"
                color="success"
                onClick={handleOpen}
                size="large"
                sx={{
                    display: "flex",
                    marginLeft: "auto",
                    marginRight: "auto",
                }}
            >
                Lets Play
            </Button>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <Typography variant="h6" sx={{ fontWeight: "bold", marginLeft: "30px", padding: "20px" }}>
                        Challengers vs. Soju Squad
                    </Typography>

                    <div style={{ display: "flex", marginTop: "-25px" }}>
                        <Typography id="modal-modal-description" sx={{ mt: 2, marginLeft: "50px" }}>
                            {selectedPlayers.map((player) => (
                                <span style={{ lineHeight: "35px" }} key={player.id}>{player.full_name} <br /></span>
                            ))}
                        </Typography>

                        <Typography id="modal-modal-description" sx={{ mt: 2, marginLeft: "80px" }}>
                            {sojuSquad.map((player) => (
                                <span style={{ lineHeight: "35px" }} key={player.id}>{player.name}<br /></span>
                            ))}
                        </Typography>
                    </div>

                    {shuffledPlayers.length > 0 && (
                        <>
                            <Typography variant="h6" sx={{ fontWeight: "bold", marginLeft: "110px", padding: "10px" }}>
                                Play By Play
                            </Typography>
                            <Box sx={{ fontSize: '14px', lineHeight: "30px" }}>
                                {playByPlayLines.map((line, index) => (
                                    <div
                                        key={index}
                                        dangerouslySetInnerHTML={{ __html: line }}
                                    ></div>
                                ))}
                            </Box>
                            <Typography variant="h6" sx={{ fontWeight: "bold", marginLeft: "80px", padding: "10px" }}>
                                {winningTeam}
                            </Typography>
                        </>
                    )}
                </Box>
            </Modal>
        </div>
    )
}

export default GameModal


