import React from 'react';
import Alert from '@mui/material/Alert';

function Message({ variant, children }) {
  return (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
      <Alert
        severity={variant}
        style={{
          width: '600px',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          textAlign: 'center',
        }}
      >
        {children}
      </Alert>
    </div>
  );
}

export default Message;
