import * as React from "react";
import Box from "@mui/material/Box";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemAvatar from "@mui/material/ListItemAvatar";
import ListItemText from "@mui/material/ListItemText";
import Avatar from "@mui/material/Avatar";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";

const SojuSquad = ({sojuSquad}) => {
    return (
        <Box sx={{ flexGrow: 1 }}>
            <Grid container spacing={2}>
                <Grid
                    sx={{
                        width: "100%",
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                    }}
                >
                    <List
                        sx={{
                            flexGrow: 0.3,
                            display: "flex",
                            flexDirection: "row",
                            flexWrap: "wrap",
                            alignItems: "center",
                            justifyContent: "center",
                        }}
                    >
                        {sojuSquad.map((selection) => (
                            <ListItem
                                key={selection.id}
                                sx={{
                                    flexBasis: "19%",
                                    minWidth: "19vw",
                                    maxWidth: "19vw",
                                    "@media screen and (max-width: 750px)": {
                                        flexBasis: "33%",
                                        minWidth: "33vw",
                                        maxWidth: "33vw",
                                    },
                                    "@media screen and (max-width: 425px)": {
                                        flexBasis: "50%",
                                        minWidth: "50vw",
                                        maxWidth: "50vw",
                                    },
                                    "&:first-of-type": {
                                        marginLeft: "5vw",
                                      },
                                }}
                            >
                                <ListItemAvatar>
                                    <Avatar
                                        src={selection.image}
                                        alt={selection.name}
                                        sx={{
                                            width: 100, height: 100, marginRight: "8px",
                                            "@media screen and (max-width: 1268px)": {
                                                width: 75,
                                                height: 75,
                                                marginRight: "6px",
                                            },
                                            "@media screen and (max-width: 968px)": {
                                                width: 50,
                                                height: 50,
                                                marginRight: "4px",
                                            }
                                        }}
                                    />
                                </ListItemAvatar>
                                <ListItemText
                                    primary={
                                        <Typography
                                            variant="h6"
                                            sx={{
                                                fontSize: '15px',
                                                "@media screen and (max-width: 1268px)": {
                                                    fontSize: '13px'
                                                },
                                                "@media screen and (max-width: 968px)": {
                                                    fontSize: '10px'
                                                },
                                                "@media screen and (max-width: 525px)": {
                                                    fontSize: '8px'
                                                }
                                            }}
                                        >
                                            {selection.name}
                                        </Typography>
                                    }
                                    secondary={
                                        <Typography
                                            variant="subtitle1"
                                            sx={{
                                                color: "#00000099",
                                                fontSize: '13px',
                                                "@media screen and (max-width: 1268px)": {
                                                    fontSize: '11px'
                                                },
                                                "@media screen and (max-width: 968px)": {
                                                    fontSize: '9px'
                                                },
                                                "@media screen and (max-width: 525px)": {
                                                    fontSize: '7px'
                                                }
                                            }}
                                        >
                                            {selection.role}
                                        </Typography>
                                    }
                                />
                            </ListItem>
                        ))}
                    </List>
                </Grid>
            </Grid>
        </Box>
    );
};

export default SojuSquad;
