import React from "react";
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Avatar from '@mui/material/Avatar';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import ListItemText from '@mui/material/ListItemText';
import IconButton from '@mui/material/IconButton';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Typography from '@mui/material/Typography';


const PlayerInfo = ({ playerData, onAddPlayer }) => {
  return (
    <div>
      <Box sx={{ flexGrow: 1 }}>
        <Grid container spacing={2}>
          <Grid item
            sx={{
              width: '100%',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
            }}>
            <List>
              {playerData.players.map((player, index) => (
                <ListItem
                  key={index}
                  secondaryAction={
                    <IconButton edge="end" aria-label="delete" onClick={() => onAddPlayer(player)}>
                      <AddCircleIcon />
                    </IconButton>
                  }
                  sx={{
                    width: '100%',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                  }}
                >
                  <ListItemAvatar>
                    <Avatar
                      src={player.image_url}
                      alt={player.full_name}
                      sx={{
                        width: 175, height: 175,
                        "@media screen and (max-width: 968px)": {
                          width: 100,
                          height: 100,
                        },
                      }}
                    />
                  </ListItemAvatar>
                  <ListItemText
                    primary={
                    <Typography 
                      variant="h4"
                      sx={{
                        "@media screen and (max-width: 968px)": {
                          fontSize: '24px'
                      },
                      }}>{player.full_name}</Typography>}
                    secondary={
                      <React.Fragment>
                        <span style={{ marginLeft: '15px' }}>{player.team_city} {player.team_name}</span><br />
                        <span style={{ marginLeft: '15px' }}>{player.height.replace('-', "'")} {player.position}</span><br />
                        <span style={{ marginLeft: '15px' }}>2022-23 PTS: {player.points}  REB: {player.rebounds}  AST: {player.assists}</span><br />
                      </React.Fragment>
                    }
                  />
                </ListItem>
              ))}
            </List>
          </Grid>
        </Grid>
      </Box>
    </div>
  );
};

export default PlayerInfo;
